.compare {
  &__bikes {
    @include page-grid;

    row-gap: 40px;
  }

  &__product {
    color: $text-color-description;
    grid-column: 1/-1;

    @include on-tablet {
      grid-column: 2/-2;
    }

    @include on-desktop {
      grid-column: span 4;
    }

  &:hover{
    transform: scale(1.1);
  }}
}
